@import "Commons.scss";

.login {
  width: 100%;
}
.login_container {
  border: #007bff;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  @include mobile {
    width: 80%;
  }
  @include tablet {
    width: 30%;
  }
  @include desktop {
    width: 30%;
  }
  input {
    padding-left: 1rem;
    padding-right: 1rem;
    width: 100%;
    height: $basc_height;
    margin: 0.22rem;
    &:focus {
      font-weight: bold;
      border-color: #007bff; // 포커스시 테두리 색상 변경
      outline: none; // 기본 포커스 효과 제거
      //background-color: aquamarine;
      box-shadow: 0 0 5px rgba(0, 123, 255, 0.5); // 포커스시 그림자 효과 추가
    }
  }
}
button {
  height: $basc_height;
}
.login_logo {
  width: 100%;
  flex: auto;
  justify-content: center;
}
.buttons {
  .btn_yes {
    width: 100%;
  }
}
.inputbox_data {
  width: 100%;
  background-color: aliceblue;
}
