@import "Font.css";

$base_font: "NotoSansKR-Regular", sans-serif;
$base_font_Bold: "NotoSansKR-Bold", sans-serif;

$mobile: 780px;
$tablet-min: 781px;
$tablet-max: 1023px;
$desktop: 1024px;
$base_magin: 0.1em;
$basc_height: 1.8rem;

@mixin mobile {
  @media (max-width: $mobile) {
    @content;
  }
}
@mixin tablet {
  @media (min-width: $tablet-min) and (max-width: $tablet-max) {
    @content;
  }
}
@mixin desktop {
  @media (min-width: $desktop) {
    @content;
  }
}

* {
  box-sizing: border-box;
  font-family: $base_font;
  border-radius: 5px;
  margin: $base_magin;
  @include mobile {
    font-size: 0.85rem;
  }
  @include tablet {
    font-size: 1rem;
  }
  @include desktop {
    font-size: 1rem;
  }
}

header {
  @include mobile() {
  }
}

body {
  height: calc(100vh - 5px);
  background: linear-gradient(
    60deg,
    #ff000088,
    palegreen
  ); /* 왼쪽에서 오른쪽으로 빨간색에서 초록색으로 변하는 그라데이션 */
}

input {
  border: 1px solid #ccc;
  box-sizing: border-box;
}

//Custom Button
button {
  font-family: $base_font_Bold;
  color: white;
  background-color: tomato;
  border: none;
  &:hover {
    background-color: rgb(0, 153, 255);
  }
}
